import {
  IDataPoint,
  IDataPointGroup,
  IDataPointValue,
  ITaxonomy,
  State,
  TaxonomyEntryType,
} from "../../types";
import { getActiveValue, mapTextToValue } from "../util";

export interface ISelectBlockAction {
  blockText: string;
  idRef: string;
  replaceValue: boolean;
  taxonomy: ITaxonomy;
  type: "SELECT BLOCK";
}

const CONFIRMATION_MESSAGE = "Are you sure you want to update the supporting text for this answer?";

export const selectBlock = (state: State, action: ISelectBlockAction): State => {
  const activeValue = getActiveValue(state);
  if (activeValue === null) {
    return state;
  }
  const {
    activeDataPoint: { groupIndex, groupKey, index, key },
    dataPoints,
  } = state;
  if (!(key in action.taxonomy)) {
    return state;
  }
  let currentValue: string | null = null;
  let updatedValue: IDataPointValue<any> | null = null;
  if (dataPoints[groupKey].isGroup) {
    const group = dataPoints[groupKey] as IDataPointGroup;
    currentValue = group.values[groupIndex][key][index].idRef;
    updatedValue = {
      ...group.values[groupIndex][key][index],
      idRef: action.idRef,
    };
  } else {
    const nonGroup = dataPoints[key] as IDataPoint;
    currentValue = nonGroup.values[index].idRef;
    updatedValue = {
      ...nonGroup.values[index],
      idRef: action.idRef,
    };
  }
  if (
    currentValue !== null &&
    currentValue !== action.idRef &&
    !window.confirm(CONFIRMATION_MESSAGE)
  ) {
    return state;
  }
  if (action.replaceValue || action.taxonomy[key].type === TaxonomyEntryType.Pointer) {
    try {
      const [value, valueRaw] = mapTextToValue(action.blockText, action.taxonomy[key]);
      updatedValue = {
        ...updatedValue,
        value,
        valueRaw,
      };
    } catch (err) {
      // do nothing, we only make a best effort to map the string to the value
    }
  }
  if (dataPoints[groupKey].isGroup) {
    const group = dataPoints[groupKey] as IDataPointGroup;
    return {
      ...state,
      dataPoints: {
        ...dataPoints,
        [groupKey]: {
          ...group,
          values: [
            ...group.values.slice(0, groupIndex),
            {
              ...group.values[groupIndex],
              [key]: [
                ...group.values[groupIndex][key].slice(0, index),
                updatedValue,
                ...group.values[groupIndex][key].slice(index + 1),
              ],
            },
            ...group.values.slice(groupIndex + 1),
          ],
        },
      },
      modified: true,
    };
  }
  return {
    ...state,
    dataPoints: {
      ...dataPoints,
      [key]: {
        ...(dataPoints[key] as IDataPoint),
        values: [
          ...(dataPoints[key] as IDataPoint).values.slice(0, index),
          updatedValue,
          ...(dataPoints[key] as IDataPoint).values.slice(index + 1),
        ],
      },
    },
    modified: true,
  };
};
