import cx from "classnames";
import moment from "moment";
import React, { memo, useCallback, useMemo } from "react";

import { WorkbenchDateFormats } from "@ui/util/dateFormats";
import { StateCallbacks } from "../../state";
import { IDataPoint, IDataPointGroup, IDataPointGroupValue, TaxonomyEntryType } from "../../types";
import { AddButton } from "../AddButton";
import { DuplicateButton } from "../DuplicateButton";
import { RemoveButton } from "../RemoveButton";

type Props = Readonly<{
  collapsed: boolean;
  group: IDataPointGroup;
  index: number;
  stateCallbacks: StateCallbacks;
  review: boolean;
  value: IDataPointGroupValue;
}>;

const getTitleOfCollapsedValues = (group: IDataPointGroup, index: number): string => {
  for (const dataPointKey of Object.keys(group.dataPoints)) {
    const dataPoint = group.dataPoints[dataPointKey] as IDataPoint;
    switch (dataPoint.type) {
      case TaxonomyEntryType.Date:
      case TaxonomyEntryType.DateTime:
      case TaxonomyEntryType.Time:
        for (const value of group.values[index][dataPointKey]) {
          if (value.valueRaw.length > 0) {
            return `${dataPoint.label}: ${moment(value.value).format(
              WorkbenchDateFormats[dataPoint.type],
            )}`;
          }
        }
        break;
      case TaxonomyEntryType.Number:
        for (const value of group.values[index][dataPointKey]) {
          if (value.valueRaw.length > 0) {
            return `${dataPoint.label}: ${value.value.toString(10)}`;
          }
        }
        break;
      case TaxonomyEntryType.Enum:
      case TaxonomyEntryType.Selection:
      case TaxonomyEntryType.String:
        for (const value of group.values[index][dataPointKey]) {
          if (value.valueRaw.length > 0) {
            return `${dataPoint.label}: ${value.value}`;
          }
        }
        break;
      default:
        break;
    }
  }
  return "(Empty)";
};

const MultipleGroupActionsComponent = ({
  collapsed,
  group,
  index,
  stateCallbacks,
  review,
  value,
}: Props) => {
  const title = useMemo(() => getTitleOfCollapsedValues(group, index), [group, index]);
  const onAddNewGroupAnswer = useCallback(() => {
    stateCallbacks.onAddNewGroupAnswer(group.key);
  }, [group.key, stateCallbacks.onAddNewGroupAnswer]);
  const onDuplicateGroupAnswer = useCallback(() => {
    stateCallbacks.onDuplicateGroupAnswer(group.key, value);
  }, [group.key, stateCallbacks.onDuplicateGroupAnswer, value]);
  const onRemoveGroup = useCallback(() => {
    stateCallbacks.onRemoveGroup(group.key, index);
  }, [index, group.key, stateCallbacks.onRemoveGroup]);
  const onToggleGroupCollapse = useCallback(() => {
    stateCallbacks.onToggleGroupCollapse(group.key, index);
  }, [index, group.key, stateCallbacks.onToggleGroupCollapse]);

  if (!group.multiple) {
    return null;
  }
  return (
    <div className="d-flex">
      <div
        className="group-title mr-auto text-muted text-truncate visible"
        onClick={e => {
          e.stopPropagation();
          if (!collapsed) {
            return;
          }
          onToggleGroupCollapse();
        }}
      >
        #{index + 1}. {title}
      </div>
      <div className="mr-3">
        <a
          href="#"
          onClick={e => {
            e.preventDefault();
            e.stopPropagation();
            onToggleGroupCollapse();
          }}
          title="Collapse/Expand"
        >
          <i
            className={cx("fa", {
              "fa-caret-down": collapsed,
              "fa-caret-up": !collapsed,
            })}
          />
        </a>
      </div>
      <div className="mr-2">
        <AddButton disabled={false} onClick={onAddNewGroupAnswer} />
      </div>
      <div className="mr-2">
        <DuplicateButton disabled={false} onClick={onDuplicateGroupAnswer} />
      </div>
      <div>
        <RemoveButton clear={group.values.length < 2} onClick={onRemoveGroup} review={review} />
      </div>
    </div>
  );
};
MultipleGroupActionsComponent.displayName = "MultipleGroupActions";
export const MultipleGroupActions = memo(MultipleGroupActionsComponent);
