import React, { memo } from "react";

import { extractTableRows } from "../../../state/getDefaultState";
import { Row as RowType, TableContent } from "../../../types";
import { IFontInformation, Props as ContentProps } from "../types";
import { Row } from "./Row";

type Props = Readonly<{
  content: ContentProps<TableContent>["content"];
  corrections?: Array<string>;
  id: string;
  fontInformation: IFontInformation | undefined;
  onSelectBlock: ContentProps<TableContent>["onSelectBlock"];
  selected: ContentProps<TableContent>["selected"];
  selectedCell: ContentProps<TableContent>["selectedCell"];
  setMouseWithin: (within: boolean) => void;
}>;

const TableComponent = ({
  content,
  corrections,
  id,
  fontInformation,
  onSelectBlock,
  selected,
  selectedCell,
  setMouseWithin,
}: Props) => {
  const body = content.blocks.reduce((prev, next, index) => {
    if (!corrections) {
      return prev.concat(next.rows);
    }
    const clone = next.element.cloneNode() as Element;
    clone.innerHTML = corrections[index];
    return prev.concat(extractTableRows({ ...next, element: clone }));
  }, [] as Array<RowType>);
  return (
    <table
      className="table table-bordered table-sm table-striped"
      id={`support-${id}`}
      onMouseEnter={() => {
        setMouseWithin(true);
      }}
      onMouseLeave={() => {
        setMouseWithin(false);
      }}
    >
      <tbody>
        {body.map((r, rowIndex) => (
          <Row
            key={`row-${rowIndex}`}
            fontInformation={fontInformation}
            isHeader={false}
            onSelectBlock={onSelectBlock}
            selectedCell={selected ? null : selectedCell}
            row={r}
          />
        ))}
      </tbody>
    </table>
  );
};
TableComponent.displayName = "Table";
export const Table = memo(TableComponent);
