import { State } from "../../types";
import { selectNewQuestion } from "./selectNewQuestion";

export interface IMoveToPrevDataPointAction {
  type: "MOVE TO PREV DATA POINT";
}

export const moveToPrevDataPoint = (state: State, action: IMoveToPrevDataPointAction): State => {
  const { activeDataPoint, dataPoints, keyOrder } = state;
  if (activeDataPoint.groupIndex < 0) {
    if (keyOrder.length < 1) {
      return state;
    }
    const toReturn = selectNewQuestion(state, {
      key: keyOrder[keyOrder.length - 1],
      type: "SELECT QUESTION",
    });
    const lastDataPoint = dataPoints[toReturn.activeDataPoint.groupKey];
    if (!lastDataPoint.isGroup) {
      return toReturn;
    }
    return {
      ...toReturn,
      activeDataPoint: {
        ...toReturn.activeDataPoint,
        key: lastDataPoint.keyOrder[lastDataPoint.keyOrder.length - 1],
      },
    };
  }
  const prevIndex = state.activeDataPoint.index - 1;
  if (prevIndex >= 0) {
    // our index is non-negative, so just decrement it
    return {
      ...state,
      activeDataPoint: {
        ...state.activeDataPoint,
        index: prevIndex,
      },
    };
  }
  const active = dataPoints[activeDataPoint.groupKey];
  if (active.isGroup) {
    const prevGroupDataPointKeyIndex =
      active.keyOrder.findIndex(v => v === activeDataPoint.key) - 1;
    if (prevGroupDataPointKeyIndex >= 0) {
      const prevKey = active.keyOrder[prevGroupDataPointKeyIndex];
      return {
        ...state,
        activeDataPoint: {
          ...state.activeDataPoint,
          index: active.values[activeDataPoint.groupIndex][prevKey].length - 1,
          key: prevKey,
        },
      };
    }
    if (activeDataPoint.groupIndex > 0) {
      const isCollapsed = active.collapsedValues.includes(activeDataPoint.groupIndex - 1);
      const lastKey = active.keyOrder[active.keyOrder.length - 1];
      if (!isCollapsed) {
        return {
          ...state,
          activeDataPoint: {
            ...state.activeDataPoint,
            groupIndex: activeDataPoint.groupIndex - 1,
            index: active.values[activeDataPoint.groupIndex - 1][lastKey].length - 1,
            key: lastKey,
          },
        };
      } else {
        return {
          ...state,
          activeDataPoint: {
            ...state.activeDataPoint,
            groupIndex: activeDataPoint.groupIndex - 1,
            index: active.values[activeDataPoint.groupIndex - 1][lastKey].length - 1,
            key: lastKey,
          },
          dataPoints: {
            ...state.dataPoints,
            [active.key]: {
              ...active,
              collapsedValues: active.collapsedValues.filter(
                v => v !== activeDataPoint.groupIndex - 1,
              ),
            },
          },
        };
      }
    }
  }
  // find the previous data point and jump to it
  const prevDataPointKeyIndex = keyOrder.findIndex(v => v === activeDataPoint.groupKey) - 1;
  if (prevDataPointKeyIndex < 0) {
    return state;
  }
  const prevGroupKey = keyOrder[prevDataPointKeyIndex];
  const prevDataPoint = state.dataPoints[prevGroupKey];
  if (prevDataPoint.values.length < 1) {
    const toReturn = selectNewQuestion(state, {
      key: prevGroupKey,
      type: "SELECT QUESTION",
    });
    const lastDataPoint = dataPoints[toReturn.activeDataPoint.groupKey];
    if (!lastDataPoint.isGroup) {
      return toReturn;
    }
    return {
      ...toReturn,
      activeDataPoint: {
        ...toReturn.activeDataPoint,
        key: lastDataPoint.keyOrder[lastDataPoint.keyOrder.length - 1],
      },
    };
  }
  if (prevDataPoint.isGroup) {
    // select the very last data point
    const isCollapsed = prevDataPoint.collapsedValues.includes(prevDataPoint.values.length - 1);
    if (!isCollapsed) {
      return {
        ...state,
        activeDataPoint: {
          groupIndex: prevDataPoint.values.length - 1,
          groupKey: prevGroupKey,
          index:
            prevDataPoint.values[prevDataPoint.values.length - 1][
              prevDataPoint.keyOrder[prevDataPoint.keyOrder.length - 1]
            ].length - 1,
          key: prevDataPoint.keyOrder[prevDataPoint.keyOrder.length - 1],
        },
      };
    } else {
      return {
        ...state,
        activeDataPoint: {
          groupIndex: prevDataPoint.values.length - 1,
          groupKey: prevGroupKey,
          index:
            prevDataPoint.values[prevDataPoint.values.length - 1][
              prevDataPoint.keyOrder[prevDataPoint.keyOrder.length - 1]
            ].length - 1,
          key: prevDataPoint.keyOrder[prevDataPoint.keyOrder.length - 1],
        },
        dataPoints: {
          ...state.dataPoints,
          [prevDataPoint.key]: {
            ...prevDataPoint,
            collapsedValues: prevDataPoint.collapsedValues.filter(
              v => v !== prevDataPoint.values.length - 1,
            ),
          },
        },
      };
    }
  }
  return {
    ...state,
    activeDataPoint: {
      groupIndex: 0,
      groupKey: prevGroupKey,
      index: prevDataPoint.values.length - 1,
      key: prevGroupKey,
    },
  };
};
