import moment from "moment";

import { toNumber } from "@core/lib/toNumber";
import { InnodomDateFormats, WorkbenchDateFormats } from "@ui/util/dateFormats";
import {
  IDataPoint,
  IDataPointGroup,
  IDataPointValue,
  State,
  TaxonomyEntry,
  TaxonomyEntryType,
} from "../types";

export const newDefaultValue = (type: TaxonomyEntryType): IDataPointValue<any> => ({
  confidence: 1.0,
  idRef: null,
  value: "",
  valueRaw: "",
});

export const isNonEmptyValue = (v: IDataPointValue<any>): boolean =>
  v.idRef !== null || (v.value !== null && v.value !== "" && v.valueRaw !== "");

export const addNewDefaultValue = (dataPoint: IDataPoint): IDataPoint => ({
  ...dataPoint,
  values: [...dataPoint.values, newDefaultValue(dataPoint.type)],
});
export const addNewDefaultValueToGroup = (group: IDataPointGroup): IDataPointGroup => {
  const newValue: { [key: string]: Array<IDataPointValue<any>> } = {};
  for (const dataPointKey of Object.keys(group.dataPoints)) {
    newValue[dataPointKey] = [newDefaultValue(group.dataPoints[dataPointKey].type)];
  }
  return {
    ...group,
    values: [...group.values, newValue],
  };
};

export const getActiveDataPoint = (state: State): IDataPoint | IDataPointGroup | null => {
  const {
    activeDataPoint: { groupKey, key },
    dataPoints,
  } = state;
  if (groupKey in dataPoints) {
    if (dataPoints[groupKey].isGroup) {
      const group = dataPoints[groupKey] as IDataPointGroup;
      if (key in group.dataPoints) {
        return group.dataPoints[key] as IDataPoint;
      }
    } else if (groupKey === key) {
      return dataPoints[key];
    }
  }
  return null;
};
export const getActiveValue = (state: State): IDataPointValue<any> | null => {
  const { activeDataPoint, dataPoints } = state;
  if (activeDataPoint.groupKey in dataPoints) {
    if (dataPoints[activeDataPoint.groupKey].isGroup) {
      const group = dataPoints[activeDataPoint.groupKey] as IDataPointGroup;
      if (
        group.values[activeDataPoint.groupIndex] !== undefined &&
        activeDataPoint.key in group.values[activeDataPoint.groupIndex]
      ) {
        return (
          group.values[activeDataPoint.groupIndex][activeDataPoint.key][activeDataPoint.index] ||
          null
        );
      }
    } else if (activeDataPoint.groupKey === activeDataPoint.key) {
      const nonGroup = dataPoints[activeDataPoint.groupKey] as IDataPoint;
      return nonGroup.values[activeDataPoint.index] || null;
    }
  }
  return null;
};

const unhandledType = (t: never): never => {
  throw new Error(`Unhandled taxonomy entry type: "${t}".`);
};

const falseValues = ["false", "no", "f", "n"];
const trueValues = ["true", "yes", "t", "y"];
const getBooleanFromString = (raw: string): [boolean, string] => {
  const split = raw
    .split(" ")
    .map(s => s.trim().toLowerCase())
    .filter(s => s.length > 0);
  if (split.length < 1) {
    return [false, "false"];
  }
  for (const word of split) {
    if (falseValues.includes(word)) {
      return [false, "false"];
    }
    if (trueValues.includes(word)) {
      return [true, "true"];
    }
  }
  throw new Error("Unable to extract boolean value.");
};

const getNumberFromString = (raw: string): [number, string] => {
  const split = raw.split(" ");
  for (const word of split) {
    const num = toNumber(word);
    if (!isNaN(num)) {
      return [num, num.toString(10)];
    }
  }
  throw new Error("Unable to extract number value.");
};

const getDateFromString = (raw: string, type: TaxonomyEntryType): [Date, string] => {
  const split = raw
    .split(" ")
    .map(s => s.trim())
    .filter(s => s.length > 0);
  for (const word of split) {
    const parsed = moment(word, WorkbenchDateFormats.allSupported);
    if (parsed.isValid()) {
      return [parsed.toDate(), parsed.format(InnodomDateFormats[type])];
    }
  }
  throw new Error("Unable to extract date value.");
};

const getEnumValueFromString = (raw: string, options: Array<string>): [string, string] => {
  if (options.length < 1 || raw.length < 1) {
    throw new Error("Unable to extract enum value.");
  }
  const cleanRaw = raw.trim().toLowerCase();
  let minIndex = -1;
  let toReturn: null | string = null;
  for (const option of options) {
    const index = cleanRaw.indexOf(option.trim().toLowerCase());
    if (index < 0) {
      continue;
    } else if (minIndex >= 0 && index > minIndex) {
      continue;
    }
    minIndex = index;
    toReturn = option;
  }
  if (toReturn === null) {
    throw new Error("Unable to extract enum value.");
  }
  return [toReturn, toReturn];
};

/**
 * For a given raw string, attempts to extract the semantic value associated with that string.
 * Throws an exception if extracting a semantic value failed, so ensure this function call is
 * always wrapped in a try { } catch.
 * @param raw string The input string.
 * @param entry TaxonomyEntry The taxonomy entry.
 * @return [any, string] Returns the value and the raw value as a string.
 */
export const mapTextToValue = (raw: string, entry: TaxonomyEntry): [any, string] => {
  switch (entry.type) {
    case TaxonomyEntryType.Boolean:
      return getBooleanFromString(raw);
    case TaxonomyEntryType.Number: {
      return getNumberFromString(raw);
    }
    case TaxonomyEntryType.Date:
    case TaxonomyEntryType.DateTime:
    case TaxonomyEntryType.Time: {
      return getDateFromString(raw, entry.type);
    }
    case TaxonomyEntryType.Selection:
      return [raw, raw];
    case TaxonomyEntryType.String:
    case TaxonomyEntryType.Pointer: {
      const trimmed = raw.trim();
      return [trimmed, trimmed];
    }
    case TaxonomyEntryType.Enum:
      return getEnumValueFromString(raw, entry.options);
    case TaxonomyEntryType.Group:
      throw new Error("Cannot set a value for a group.");
  }
  unhandledType(entry);
};
