import React, { memo, useEffect, useRef } from "react";
import { FormFeedback, Input } from "reactstrap";

import { toNumber } from "@core/lib/toNumber";
import { IInputComponentProps } from "./common";

const NumberInputComponent = ({
  active,
  dataPointKey,
  index,
  invalid,
  onFocus,
  onUpdateAnswer,
  showAlerts,
  taxonomyEntry,
  value,
}: IInputComponentProps<number>) => {
  const inputId = `input-${dataPointKey}-${index}`;
  const valid = !invalid && taxonomyEntry.required;
  const focusRef = useRef<HTMLInputElement>(null);
  useEffect(() => {
    if (!focusRef.current) {
      return;
    }
    if (active && focusRef.current.focus) {
      focusRef.current.focus();
    } else if (focusRef.current.blur) {
      focusRef.current.blur();
    }
  }, [active]);
  return (
    <>
      <Input
        id={inputId}
        innerRef={focusRef}
        inputMode="decimal"
        invalid={invalid}
        onChange={e => {
          onUpdateAnswer(toNumber(e.target.value), e.target.value);
        }}
        onFocus={() => {
          onFocus(dataPointKey, index);
        }}
        type="text"
        valid={valid}
        value={value.valueRaw}
      />
      {showAlerts && invalid && (
        <FormFeedback>Value is missing or is not a valid number.</FormFeedback>
      )}
    </>
  );
};
NumberInputComponent.displayName = "NumberInput";
export const NumberInput = memo(NumberInputComponent);
