import { RefObject, useEffect } from "react";

export const useScrollToQuestion = (container: RefObject<HTMLElement>) => {
  useEffect(() => {
    if (!container.current) {
      return;
    } else if (container.current.scrollHeight <= container.current.clientHeight) {
      return;
    }
    const activeChildren = container.current.getElementsByClassName("active");
    if (activeChildren.length < 1) {
      return;
    }
    const activeChild = activeChildren[0] as HTMLElement;
    container.current.style.scrollBehavior = "smooth";
    let offsetTop = activeChild.offsetTop;
    let parent: HTMLElement = activeChild.offsetParent as HTMLElement;
    while (!!parent && parent !== container.current) {
      offsetTop += parent.offsetTop;
      parent = parent.offsetParent as HTMLElement;
    }
    const { clientHeight } = activeChild;
    // if the element is larger than the view port, we scroll to the top of the element
    if (clientHeight > container.current.clientHeight) {
      container.current.scrollTop = offsetTop;
      return;
    }
    // center the element in the scroll view
    const offsetY = Math.floor(offsetTop - (container.current.clientHeight - clientHeight) / 2);
    container.current.scrollTop = Math.max(0, Math.min(container.current.scrollHeight, offsetY));
  });
};
