import { State } from "../../types";
import { selectNewQuestion } from "./selectNewQuestion";

export interface IMoveToNextDataPointAction {
  type: "MOVE TO NEXT DATA POINT";
}

export const moveToNextDataPoint = (state: State, action: IMoveToNextDataPointAction): State => {
  const { activeDataPoint, dataPoints, keyOrder } = state;
  if (activeDataPoint.groupIndex < 0) {
    if (keyOrder.length < 1) {
      return state;
    }
    return selectNewQuestion(state, { key: keyOrder[0], type: "SELECT QUESTION" });
  }
  const nextIndex = state.activeDataPoint.index + 1;
  const active = dataPoints[activeDataPoint.groupKey];
  // handle non-groups
  if (!active.isGroup) {
    // check if the data point has more values
    if (nextIndex < active.values.length) {
      return {
        ...state,
        activeDataPoint: {
          ...state.activeDataPoint,
          index: nextIndex,
        },
      };
    }
  } else {
    // get the current group data
    const currentGroup = active.values[activeDataPoint.groupIndex][activeDataPoint.key];
    if (nextIndex < currentGroup.length) {
      return {
        ...state,
        activeDataPoint: {
          ...state.activeDataPoint,
          index: nextIndex,
        },
      };
    }
    const nextKeyIndex = active.keyOrder.findIndex(v => v === activeDataPoint.key) + 1;
    if (nextKeyIndex < active.keyOrder.length) {
      const nextKey = active.keyOrder[nextKeyIndex];
      return {
        ...state,
        activeDataPoint: {
          ...state.activeDataPoint,
          index: 0,
          key: nextKey,
        },
      };
    }
    const nextGroupIndex = activeDataPoint.groupIndex + 1;
    if (nextGroupIndex < active.values.length) {
      // check if the next group entry is collapsed
      const isCollapsed = active.collapsedValues.includes(nextGroupIndex);
      if (!isCollapsed) {
        return {
          ...state,
          activeDataPoint: {
            ...state.activeDataPoint,
            groupIndex: nextGroupIndex,
            index: 0,
            key: active.keyOrder[0],
          },
        };
      } else {
        return {
          ...state,
          activeDataPoint: {
            ...state.activeDataPoint,
            groupIndex: nextGroupIndex,
            index: 0,
            key: active.keyOrder[0],
          },
          dataPoints: {
            ...state.dataPoints,
            [active.key]: {
              ...active,
              collapsedValues: active.collapsedValues.filter(v => v !== nextGroupIndex),
            },
          },
        };
      }
    }
  }
  const nextGroupKeyIndex = keyOrder.findIndex(v => v === activeDataPoint.groupKey) + 1;
  if (nextGroupKeyIndex < keyOrder.length) {
    const nextGroupKey = keyOrder[nextGroupKeyIndex];
    const nextActive = dataPoints[nextGroupKey];
    if (nextActive.isGroup) {
      if (nextActive.values.length < 1) {
        return selectNewQuestion(state, { key: nextGroupKey, type: "SELECT QUESTION" });
      }
      const isCollapsed = nextActive.collapsedValues.includes(0);
      if (!isCollapsed) {
        return {
          ...state,
          activeDataPoint: {
            groupIndex: 0,
            groupKey: nextGroupKey,
            index: 0,
            key: nextActive.keyOrder[0],
          },
        };
      } else {
        return {
          ...state,
          activeDataPoint: {
            groupIndex: 0,
            groupKey: nextGroupKey,
            index: 0,
            key: nextActive.keyOrder[0],
          },
          dataPoints: {
            ...state.dataPoints,
            [nextActive.key]: {
              ...nextActive,
              collapsedValues: nextActive.collapsedValues.filter(v => v !== 0),
            },
          },
        };
      }
    }
    if (nextActive.values.length < 1) {
      return selectNewQuestion(state, { key: nextGroupKey, type: "SELECT QUESTION" });
    }
    return {
      ...state,
      activeDataPoint: {
        groupIndex: 0,
        groupKey: nextGroupKey,
        index: 0,
        key: nextGroupKey,
      },
    };
  }
  return state;
};
