import { IDataPointGroup, State } from "../../types";

export interface IToggleGroupCollapseAction {
  groupIndex: number;
  groupKey: string;
  type: "TOGGLE GROUP COLLAPSE";
}

export const toggleGroupCollapse = (state: State, action: IToggleGroupCollapseAction): State => {
  const dataPoints = state.dataPoints;
  let activeDataPoint = state.activeDataPoint;
  if (action.groupKey in state.dataPoints && state.dataPoints[action.groupKey].isGroup) {
    const group = state.dataPoints[action.groupKey] as IDataPointGroup;
    if (group.values[action.groupIndex] !== undefined) {
      let collapsedValues = group.collapsedValues;
      if (collapsedValues.includes(action.groupIndex)) {
        collapsedValues = collapsedValues.filter(v => v !== action.groupIndex);
        activeDataPoint = {
          groupIndex: action.groupIndex,
          groupKey: action.groupKey,
          index: 0,
          key: group.keyOrder[0],
        };
      } else {
        collapsedValues = [...collapsedValues, action.groupIndex];
        // if the active data point is within the collapsing value, we should move to the next one
        if (
          activeDataPoint.groupIndex === action.groupIndex &&
          activeDataPoint.groupKey === action.groupKey
        ) {
          const nextGroupIndex = activeDataPoint.groupIndex + 1;
          if (nextGroupIndex < group.values.length) {
            activeDataPoint = {
              ...activeDataPoint,
              groupIndex: nextGroupIndex,
              index: 0,
              key: group.keyOrder[0],
            };
          } else {
            activeDataPoint = {
              ...activeDataPoint,
              groupIndex: -1,
              index: -1,
              key: "",
            };
          }
        }
      }
      return {
        ...state,
        activeDataPoint,
        dataPoints: {
          ...dataPoints,
          [action.groupKey]: {
            ...(dataPoints[action.groupKey] as IDataPointGroup),
            collapsedValues,
          },
        },
      };
    }
  }
  return state;
};
