import cx from "classnames";
import React, { memo, useCallback } from "react";
import { Col, Collapse, Form, Row } from "reactstrap";

import { StateCallbacks } from "../../state";
import { IDataPoint, IDataPointGroup, ITaxonomy, State } from "../../types";
import { Answer } from "../Answer";
import { MultipleGroupActions } from "./MultipleGroupActions";
import { QuestionTitle } from "./QuestionTitle";

type Props = Readonly<{
  active: State["activeDataPoint"];
  group: IDataPointGroup;
  index: number;
  review: boolean;
  stateCallbacks: StateCallbacks;
  taxonomy: ITaxonomy;
}>;

const ActiveGroupQuestionComponent = ({
  active,
  group,
  index,
  review,
  stateCallbacks,
  taxonomy,
}: Props) => {
  const showMultiple = Object.keys(group.dataPoints).some(dp => group.dataPoints[dp].multiple);
  const onSubmit = useCallback(e => {
    e.preventDefault();
  }, []);
  return (
    <Form onSubmit={onSubmit}>
      <QuestionTitle index={index} label={group.label} />
      <Row>
        <Col xs={12}>
          {group.values.map((v, i) => {
            const collapsed = group.collapsedValues.includes(i);
            return (
              <div
                className={cx("grouped-container", {
                  border: group.multiple,
                  "border-secondary": group.multiple,
                })}
                key={`group-${group.key}-value-${i}`}
              >
                <MultipleGroupActions
                  collapsed={collapsed}
                  group={group}
                  index={i}
                  review={review}
                  stateCallbacks={stateCallbacks}
                  value={v}
                />
                <Collapse isOpen={!collapsed}>
                  {group.keyOrder.map(dataPointKey =>
                    v[dataPointKey].map((innerValue, j) => (
                      <Answer
                        active={
                          active.groupKey === group.key &&
                          active.groupIndex === i &&
                          active.key === dataPointKey &&
                          active.index === j
                        }
                        dataPoint={group.dataPoints[dataPointKey] as IDataPoint}
                        disableAdd={!group.dataPoints[dataPointKey].multiple}
                        disableRemove={
                          !group.dataPoints[dataPointKey].multiple || v[dataPointKey].length < 2
                        }
                        key={`answer-${j}`}
                        groupIndex={i}
                        groupKey={group.key}
                        index={j}
                        isGroupQuestion
                        label={group.dataPoints[dataPointKey].label}
                        review={review}
                        showLabel
                        showMultiple={showMultiple}
                        stateCallbacks={stateCallbacks}
                        taxonomyEntry={taxonomy[dataPointKey]}
                        value={innerValue}
                      />
                    )),
                  )}
                </Collapse>
                {!collapsed && (
                  <MultipleGroupActions
                    collapsed={collapsed}
                    group={group}
                    index={i}
                    review={review}
                    stateCallbacks={stateCallbacks}
                    value={v}
                  />
                )}
              </div>
            );
          })}
        </Col>
      </Row>
    </Form>
  );
};
ActiveGroupQuestionComponent.displayName = "ActiveGroupQuestion";
export const ActiveGroupQuestion = memo(ActiveGroupQuestionComponent);
