import cx from "classnames";
import React, { CSSProperties, memo } from "react";

import { Cell, ContentType, Page as PageType, State, TableContent } from "../../types";
import { TABLE_SELECT_CONFIRMATION } from "../InnodomContent/Table";

type Props = Readonly<{
  activeBlockId: string | null;
  containerWidth: number;
  confirmTableSelection: boolean;
  content: State["content"];
  disableTableSelectionConfirmation: () => void;
  onSelectBlock: (id: string, text: string, replaceValue: boolean) => void;
  page: PageType;
  zoom: number;
}>;

const NULL_CELL: CSSProperties = {
  bottom: 0,
  left: 0,
  pointerEvents: "none",
  right: 0,
  top: 0,
  zIndex: 1199,
};

const GUTTER = 20;

const PageComponent = ({
  activeBlockId,
  confirmTableSelection,
  containerWidth,
  content,
  disableTableSelectionConfirmation,
  onSelectBlock,
  page,
  zoom,
}: Props) => {
  const scale = ((containerWidth - 2 * GUTTER) / page.width) * zoom;
  const contents = page.blocks.map(b => {
    const zoneId = `zone-${b.contentId}`;
    const style = {
      height: b.height * scale,
      left: b.x * scale + GUTTER,
      top: b.y * scale,
      width: b.width * scale,
    };
    const selected = !!activeBlockId && b.contentId === activeBlockId;
    const parentContent = content[b.contentId];
    if (!parentContent) {
      return null;
    }
    switch (b.type) {
      case ContentType.Table: {
        const tableContent = parentContent as TableContent;
        const cells: Array<Cell> = b.rows.reduce(
          (prev, next) => prev.concat(next.cells),
          [] as Array<Cell>,
        );
        return (
          <React.Fragment key={zoneId}>
            <div
              className={cx("zone", { selected })}
              id={zoneId}
              onClick={e => {
                e.preventDefault();
                if (confirmTableSelection && !window.confirm(TABLE_SELECT_CONFIRMATION)) {
                  return;
                }
                disableTableSelectionConfirmation();
                onSelectBlock(b.contentId, tableContent.text, e.ctrlKey);
              }}
              style={style}
            />
            {cells.map(cell => {
              const cellSelected = !!activeBlockId && activeBlockId === cell.id;
              if (isNaN(cell.height) || isNaN(cell.width) || isNaN(cell.x) || isNaN(cell.y)) {
                return (
                  <div
                    className={cx("zone", "zone-cell", { selected: cellSelected })}
                    key={cell.id}
                    id={`zone-${cell.id}`}
                    onClick={e => {
                      e.preventDefault();
                    }}
                    style={NULL_CELL}
                  />
                );
              }
              return (
                <div
                  className={cx("zone", "zone-cell", { selected: cellSelected })}
                  key={cell.id}
                  id={`zone-${cell.id}`}
                  onClick={e => {
                    e.preventDefault();
                    onSelectBlock(cell.id, cell.element.textContent || "", e.ctrlKey);
                  }}
                  style={{
                    height: cell.height * scale,
                    left: cell.x * scale + GUTTER,
                    top: cell.y * scale,
                    width: cell.width * scale,
                  }}
                />
              );
            })}
          </React.Fragment>
        );
      }
      default:
        return (
          <div
            className={cx("zone", { selected })}
            key={zoneId}
            id={zoneId}
            onClick={e => {
              let textContent = "";
              switch (b.type) {
                case ContentType.Image:
                  textContent = "Image selected";
                  break;
                case ContentType.Math:
                  textContent = "Math selected";
                  break;
                case ContentType.Text:
                  if (parentContent.type === ContentType.Text) {
                    textContent = parentContent.text;
                  }
                  break;
              }
              e.preventDefault();
              onSelectBlock(b.contentId, textContent, e.ctrlKey);
            }}
            style={style}
          />
        );
    }
  });
  const pageDimensions = {
    height: `${page.height * scale}px`,
    width: `${page.width * scale + 2 * GUTTER}px`,
  };
  return (
    <div className="page" style={pageDimensions}>
      <img className="image" src={page.backgroundSrc} />
      <div className="zones">{contents}</div>
    </div>
  );
};
PageComponent.displayName = "Page";
export const Page = memo(PageComponent);
